import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class QuestionsList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      questions: [],
      isLoggedIn: false
    };
  }

  fetchQuestions() {
    fetch('https://missionaustralia-back.herokuapp.com/getQuestions?token=' + cookies.get('token'))
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          this.setState({
            questions: (resData.data !== undefined)?resData.data:[]
          });
        } else {
          alert("Error fetching questions")
        }
      });
  }

  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }
  }

  componentDidMount() {
    this.fetchQuestions()
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col>
              <Card>
                <CardHeader>
                  <i className="fa fa-align-justify"></i> Questions List
              </CardHeader>
                <CardBody>
                  <Table hover bordered striped responsive size="sm">
                    <thead>
                      <tr>
                        <th>Title</th>
                        <th>Description</th>
                        <th>Priority</th>
                        <th>Comments</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.questions.map((question, i) => {
                        return (
                          <tr key={question._id}>
                            <td>{question.title}</td>
                            <td>{question.description}</td>
                            <td>
                              {question.priority === 'Low'
                                ? <Badge color="success">Low</Badge>
                                : question.priority === 'Medium' ? <Badge color="warning">Medium</Badge>
                                  : question.priority === 'High' ? <Badge color="danger">High</Badge>
                                    : null
                              }
                            </td>
                            <td>
                              <a href={`#/forum/question/${question._id}`}>Details</a>
                            </td>
                          </tr>);
                      })}
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default QuestionsList;